import React from "react";
import pageHeader from "@/views/Main/component-doc/ZpageHeader-doc/pageHeader";
import { Input, InputNumber, Button, Divider } from "antd";
import { ZeditSimpleFormHOC, zTool } from "zerod";
import defaultConfigData from "@/mock/serviceDefaultConfigData.js";
import MoreContent from "./moreContent";
// 后台接口
import api from "@/App.api.js";

function getConfig(pageType) {
	const configFormItems = [
		{
			key: "quick",
			label: "快捷操作",
			render: (form,changeFormItes,tool) => {
				console.log(changeFormItes,tool)
				return (

					<Button
						onClick={() => {
							form.setFieldsValue({
								confProperty: defaultConfigData.default,
							});
						}}
					>
						模板配置
					</Button>
				);
			},
		},
		{
			key: "confProperty",
			label: "默认配置",
			span:24,
			render: (form) => {
				return (
					<Input.TextArea
						rows={15}
						placeholder="请输入默认配置"
						ref={(el) => {
							zTool.scrollDisableWheel(el.textAreaRef);
						}}
					/>
				);
			},
			//antd的 form.getFieldDecorator的options
			options: {
				//验证规则
				rules: [
					{
						required: true,
						message: "不能为空。",
					},
				],
			},
		},
	];

	let formItems = [
		{
			key: "serviceCode",
			label: "服务编码",
			render: (form) => {
				return <Input placeholder="请输入服务编码" />;
			},
			//antd的 form.getFieldDecorator的options
			options: {
				//验证规则
				rules: [
					{
						required: true,
						message: "不能为空。",
					},
				],
			},
		},
		{
			key: "serviceName",
			label: "服务名称",
			render: (form) => {
				return <Input placeholder="请输入服务名称" />;
			},
			//antd的 form.getFieldDecorator的options
			options: {
				//验证规则
				rules: [
					{
						required: true,
						message: "不能为空。",
					},
				],
			},
		},
		{
			key: "remark",
			label: "服务说明",
			span:24,
			render: (form) => {
				return <Input.TextArea rows={2} placeholder="请输入服务说明" />;
			},
			//antd的 form.getFieldDecorator的options
			options: {
				//验证规则
				rules: [
					{
						required: true,
						message: "不能为空。",
					},
				],
			},
		},
		{
			key: "servicePort",
			label: "端口号",
			render: (form) => {
				return <InputNumber min={11110} max={65535} step={10} />;
			},
			//antd的 form.getFieldDecorator的options
			options: {
				//验证规则
				rules: [
					{
						required: true,
						message: "不能为空。",
					},
				],
			},
		},
	];
	if (pageType === "add") {
		formItems = [...formItems, ...configFormItems];
	}
	const pageCofig = {
		insertLocation: "mainModal",
		pageHeader: {
			show: true,
			...pageHeader,
			breadcrumbRoutes: [],
		},
		form: {
			type: pageType,
			panelHeader: pageType == "add" ? "新增服务信息" : "修改服务信息",
			items: formItems,
			detailApiInterface: (id, props) => {
				return api.config.getServiceDetail({ serviceId: id });
			},
			submitApiInterface: (values, props) => {
				if (pageType === "add") {
					return api.config.addService(Object.assign({}, values, { environment: "default" }));
				} else {
					return api.config.updateService(Object.assign({}, values, { serviceId: props.detailId }));
				}
			},
		},
		moreContentRender: function() {
			return (
				<div className="z-panel z-margin-top-20">
					<div className="z-panel-body">moreContentRender</div>
				</div>
			);
		},
		panelBeforeRender: function() {
			return (
				<div className="z-panel z-margin-bottom-20">
					<div className="z-panel-body">panelBeforeRender</div>
				</div>
			);
		},
		panelAfterRender: function() {
			return <MoreContent />;
		},
	};
	return pageCofig;
}

export default getConfig;
